import { FC, ChangeEvent, useState, useCallback } from 'react';
import { format } from 'date-fns';
import numeral from 'numeral';
import PropTypes from 'prop-types';
import { api } from 'src/api/api';
import {
  Tooltip,
  Divider,
  Box,
  FormControl,
  InputLabel,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Select,
  MenuItem,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';

import { read, utils, writeFileXLSX } from 'xlsx';
import Label from 'src/components/Label';
import {IShip} from 'src/models/product';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import BulkActions from '../Ship/BulkActions';
import {FaCloudDownloadAlt} from 'react-icons/fa'
import { ShipModal } from 'src/components/Modals/ShipModal/ShipModal';
import http from 'src/http';

interface ShipTableProps {
  className?: string;
  ships: IShip[];
}



const applyFilters = (
  ships: IShip[],
): IShip[] => {
  return ships.filter((ship) => {
    let matches = true;

    /*if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  ships: IShip[],
  page: number,
  limit: number
): IShip[] => {
  return ships.slice(page * limit, page * limit + limit);
};

const ShipTable: FC<ShipTableProps> = ({ ships }) => {

  const [selectedShips, setSelectedShips] = useState<string[]>(
    []
  );
  const selectedBulkActions = selectedShips.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);

  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(ships);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Entrega.xlsx");
  }, [ships]);
  const handleSelectAllShips = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedShips(
      event.target.checked
        ? ships.map((ship) => ship.id)
        : []
    );
  };

  const handleSelectOneShip = (
    event: ChangeEvent<HTMLInputElement>,
    ship: string
  ): void => {
    if (!selectedShips.includes(ship)) {
      setSelectedShips((prevSelected) => [
        ...prevSelected,
        ship
      ]);
    } else {
      setSelectedShips((prevSelected) =>
        prevSelected.filter((id) => id !== ship)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredShips = applyFilters(ships);
  const paginatedShips = applyPagination(
    filteredShips,
    page,
    limit
  );
  const selectedSomeShips =
    selectedShips.length > 0 &&
    selectedShips.length < ships.length;
  const selectedAllShips =
  selectedShips.length === ships.length;


  const theme = useTheme();


  const [data, setData] = useState<IShip[]>([]);
  const [idShip, setIdShip] = useState<IShip[]>([]);
  const [modal, setModal] = useState<Boolean>(false)

  async function queryShipById(idShip: any){
    setModal(!modal)
    setData(null)
    const {data} = await http.get(`/fretes/${idShip}`);
    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }


  return (
    <Card>
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions id_ship={selectedShips}/>
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
                  <FaCloudDownloadAlt  onClick={exportFile} style={{
                  textAlign: 'right',
                  position: 'relative',
                  left: '7rem',
                  height: '25px',
                  width: '25px',
                  cursor: 'pointer'
               }}/>
            </Box>
          }
          title="Regiões cadastradas"
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                
              </TableCell>
              <TableCell>Descrição</TableCell>
              <TableCell>CEP Saída</TableCell>
              <TableCell>CEP Destino</TableCell>
              <TableCell>Valor</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedShips.map((ship) => {
              const isShipSelected = selectedShips.includes(
                ship.id
              );
              return (
                <TableRow
                  hover
                  key={ship.id}
                  selected={isShipSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isShipSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneShip(event, ship.id)
                      }
                      value={isShipSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {ship?.nome}
                    </Typography>
                    </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {ship?.cep_inicio}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {ship?.cep_fim}
                    </Typography>
                  </TableCell>

                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {ship?.valor.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})}
                    </Typography>
                  </TableCell>

             
                  <TableCell align="right">
                    <Tooltip title="Editar categoria" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryShipById(ship?.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                    {/*<Tooltip title="Excluir produto" arrow>
                      <IconButton
                        sx={{
                          '&:hover': { background: theme.colors.error.lighter },
                          color: theme.palette.error.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <DeleteTwoToneIcon fontSize="small" />
                      </IconButton>
                      </Tooltip>*/}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredShips.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      {data  && modal &&(
        <ShipModal 
        openModal={modal}
        setModal={setModal}
        data={data}
        edit={true}
      />
      )}
    </Card>
  );
};

ShipTable.propTypes = {
  ships: PropTypes.array.isRequired
};

ShipTable.defaultProps = {
  ships: []
};

export default ShipTable;
